
.auth {
  text-align: center;
}
.btn-submit {
  margin-top: 5px;
  width: 300px;
}
.form__input {
  margin-bottom: 10px;
}
.field {
  width: 300px;
  height: 35px;
  line-height: 35px;
  background-color: #e6e6e6;
  border-radius: 7px;
  border: 0;
  padding-left: 30px;
}
.field::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #3e3e3e;
}
.field:-ms-input-placeholder { /* IE 10+ */
  color: #3e3e3e;
}
.field::placeholder {
  color: #3e3e3e;
}



