
.app-container {
  width: 375px;
  height: 420px;
}
.content-wrapper {
  width: 355px;
  height: 360px;
  margin: 10px auto 0;
}
body {
  font-size:13px;
  height: 100%;
  -webkit-font-smoothing: antialiased;
}
button:focus {
  outline: none;
}
.btn-std {
  padding: 7px 10px;
  font-weight: bold;
  color: #fff;
  background-color: #283d59;
  border-radius: 7px;
  border: 1px solid transparent;
  -webkit-transition: background 0.5s ease;
  transition: background 0.5s ease;
}
.btn-std:hover, .btn-std:focus, .btn-std:active {
  background-color: #283d59;
  color: #fff;
}
.btn-inv {
  border: 1px solid #ccc;
  background-color: transparent;
  color: #283D59;
}
.btn-inv:hover, .btn-inv:focus, .btn-inv:active {
  border-color: transparent;
  background: #283D59;
  color: #fff;
}
.btn-inv:disabled {
  background-color: #f6f6f6;
  opacity: 0.5;
}
.btn-inv:disabled:hover, .btn-inv:disabled:focus {
  border: 1px solid #283D59;
  color: #283D59;
  cursor: not-allowed;
}
a.btn-std:hover, a.btn-std:focus, a.btn-std:active {
  text-decoration: none;
}



